import React, { useEffect, useRef, useState } from 'react';
import { observer } from 'mobx-react';
import Select from "react-select";
import Modal from '../modal/Modal';
import styles from './Dashboard.module.scss'
import store from '../../store/Store';
import PieChartRaw from '../../assets/fw/pieChart/pieChart';
import BarChart from '../../assets/fw/barChart/barChart'
import BarChartNoPadding from '../../assets/fw/barChartNoPadding/barChart'
import cn from 'classnames';

const Dashboard: React.FC = observer(() => {

  const [show,setShow] = useState(false) 
  const [active,setActive] = useState([true,false]) 
  const [width,setWidth] = useState('') 
  const ref = useRef<HTMLInputElement>(null)

  useEffect( () => {

    checkHeight()

    window.addEventListener('resize', () => {
      
      checkHeight()
    })

  },[])

  const checkHeight = () => {
    // console.log(width)
    if (ref && ref.current) {
      setWidth(String(ref.current.getBoundingClientRect().width))
    }
  }

  const handleShowModal = () => {
    setShow(!show)
  }

  const handleSwitch = (index:number) => {
    switch(index){
      case 0:
        setActive([true,false])
      break;
      case 1:
        setActive([false,true])
      break;
    }
  }

  const getArray = (type:string, e:any) => {

  store.storeData[type].filter=e

  let array:any = []

  if(!(store.storeData[type].filter === '' || store.storeData[type].filter.length === 0)){
    store.storeData[type].data.filter((value:any)=>
    store.storeData[type].filter.some((filter:any)=> 
    filter.label === value.number)).map((item:any)=>{
      array.push(item) 
    })
  }else {
    array = store.storeData[type].data
  }
  return array
}

const getResult = (type:string, e:any) => {

  let result = [0,0,0]

  getArray(type,e).map((item:any)=>{
    Object.keys(item).map((key,id)=>{
      switch(type == 'sportsman' ? key: item[key]){
        case type == 'sportsman' ? 'p1': 1: result[0]+= type == 'sportsman' ? item[key]: 1
      break;
        case type == 'sportsman' ? 'p2': 2: result[1]+= type == 'sportsman' ? item[key]: 1
      break;
        case type == 'sportsman' ? 'p3': 3: result[2]+= type == 'sportsman' ? item[key]: 1
      break;
        }
      })
    })
  return type == 'sportsman' ? [Math.round(result[0]/getArray(type,e).length),Math.round(result[1]/getArray(type,e).length),Math.round(result[2]/getArray(type,e).length)] :result
  }

  const getMetodData = (type:string,e:any,index:string) => {
    let r = 0
    getArray(type,e).map((item:any)=>{
      // console.log(getArray(type,e))
      Object.keys(item).map((key,id)=>{
        if(key == index){
          r += item[key]
        }
      })
    })
    return Math.round(r/getArray(type,e).length)
  }

  return <div style={{margin:'32px'}}>

      <div ref={ref} style={{display:'flex', marginBottom:'40px', alignItems:'center'}}>

      <div className={styles.header_body}>
        <div className={styles.header}> </div>
        <div 
        className={cn(styles.button_switch,active[0] && styles.button_switch_avtive)}
        onClick={()=>handleSwitch(0)}></div>
        <div 
        className={cn(styles.button_switch,active[1] && styles.button_switch_avtive)}
        onClick={()=>handleSwitch(1)}></div>
      </div>
      <div className={styles.button} onClick={()=> handleShowModal()}> </div>
      </div>
      {!active[0] && 
      <div>
      <div>
        <div  className={styles.filter}>
          <div className={styles.label_filter}></div>
          <Select
          isMulti
          placeholder={' '}
          onChange={(e)=>getMetodData('trainer',e,'p1')}
          options={store.storeData.trainer.data.map((row:any,index:any) => {
            return {value: index, label:row.number}
          })}/>
        </div>
      </div>
      <div style={{display:'flex'}}>
      <div style={{background: 'white', margin: '0 16px 32px 0', width:'30%'}}>
        <div className={styles.label}>  </div>
          <PieChartRaw
            e={store.storeData.trainer.filter}
            sum={(type:string, e:any)=>getResult(type,e)}
            type={'trainer'}
            />
      </div>
      <div style={{background: 'white',marginBottom:'32px', width:'70%'}}>
        <div className={styles.label}>   - ( )</div>
          <BarChartNoPadding
          e={store.storeData.trainer.filter}
          type={'trainer'}
          getMetodData = {(type:string, e:any, index:string)=>getMetodData(type,e,index)}
          width={width}
          />
        </div>
      </div>
      </div>
    }

    {active[0] && 
    <div>
      <div>
        <div  className={styles.filter}>
          <div className={styles.label_filter}> </div>
          <Select
          isMulti
          placeholder={' '}
          onChange={(e)=>getMetodData('sportsman',e,'m1')}
          options={store.storeData.sportsman.data.map((row:any,index:any) => {
            return {value: index, label:row.number}
          })}/>
        </div>
      </div>
      <div style={{display:'flex'}}>
        <div style={{background: 'white',marginBottom:'32px',marginRight:'16px', width:'100%'}}>
          <div className={styles.label}>    ( )</div>
            <PieChartRaw
              e={store.storeData.sportsman.filter}
              sum={(type:string, e:any)=>getResult(type,e)}
              type={'sportsman'}
              />
        </div>
        <div style={{background: 'white',marginBottom:'32px',marginRight:'16px', width:'100%'}}>
      <div className={styles.label}> </div>
 3   \\ 
      </div>
      <div style={{background: 'white',marginBottom:'32px', width:'100%'}}>
      <div className={styles.label}> </div>
 
      </div>
      </div>
      <div style={{background: 'white',marginBottom:'32px'}}>
      <div className={styles.label}>   - ( )</div>
      <BarChart
      e={store.storeData.sportsman.filter}
      getMetodData = {(type:string, e:any, index:string)=>getMetodData(type,e,index)}
      width={width}
      type={'sportsman'}
      />
      </div>
    </div>  
    }
  <Modal
  show={show} 
  handleShowModal={()=>handleShowModal()}/>
  </div>
});

export default Dashboard;