// import "./styles.css";
import { observer } from "mobx-react";
import React, { useCallback, useEffect, useState } from "react";
import { PieChart, Pie, Cell } from "recharts";
import store from "../../../store/Store";
import styles from './pieChart.module.scss' 

type propsType = {
  sum: (type:string, e:any)=>any
  e:any
  type: 'sportsman' | 'trainer'
}

const PieChartRaw = observer((props: propsType) => {

  const{sum, e, type} = props

  const getName= (type:string) => {
    let name:any = []
    switch(type){
      case 'sportsman': name = ['  ','  ','   ']
    break;
      case 'trainer': name = ["  '1'","  '2'","  '3'"]
    }
    return name
  }

  const data = [
  { name: getName(type)[0], value: sum(type,e)[0]},
  { name: getName(type)[1], value: sum(type,e)[1]},
  { name: getName(type)[2], value: sum(type,e)[2]},
];

const COLORS = ["#0088FE", "#00C49F", "#FFBB28"];

const RADIAN = Math.PI / 180;
const renderCustomizedLabel = ({
  cx,
  cy,
  midAngle,
  innerRadius,
  outerRadius,
  percent,
  index
}: any) => {
  const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
  const x = cx + radius * Math.cos(-midAngle * RADIAN);
  const y = cy + radius * Math.sin(-midAngle * RADIAN);

  return (
    <text
      x={x}
      y={y}
      fill="white"
      textAnchor={x > cx ? "start" : "end"}
      dominantBaseline="central"
    >
      {`${(percent * 100).toFixed(0)}%`}
    </text>
  );
};

return (
    <div className={styles.body}>
    <PieChart width={200} height={200}>
      <Pie
        data={data}
        cx={100}
        cy={100}
        labelLine={false}
        label={renderCustomizedLabel}
        outerRadius={80}
        fill="#8884d8"
        dataKey="value"
      >
        {data.map((entry, index) => (
          <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
        ))}
      </Pie>
    </PieChart>
    <div style={{margin:'0 32px'}}>{
      data.map((item:any,id) => {
        return <div className={styles.legend_row} >
          <div className={styles.circle} style={{background: COLORS[id]}}></div>
          <div style={{marginRight:'8px'}}>{item.name}: </div><div style={{fontWeight: '600', marginLeft: 'auto'}}>{item.value}</div></div>
      })
    }
    </div>
    </div>
  );

})
export default PieChartRaw;